@import url(https://fonts.googleapis.com/css2?family=Keania+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding:0;
  border:none;
  box-sizing: border-box;
}
body,html{
  height:100%;
}
:root{
  --yellow:#e9c46a;
  --blue:#4cc9f0;
  --red:#c1121f;
}
@-webkit-keyframes shake {
  0%{
    /* opacity:0; */
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }
  50%{
    /* opacity: 0.5; */
    -webkit-transform: rotate(-10deg) ;
            transform: rotate(-10deg) ;
  }
  100%{
    /* opacity: 1; */
    -webkit-transform: rotate(10deg) ;
            transform: rotate(10deg) ;
  }
  
}
@keyframes shake {
  0%{
    /* opacity:0; */
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }
  50%{
    /* opacity: 0.5; */
    -webkit-transform: rotate(-10deg) ;
            transform: rotate(-10deg) ;
  }
  100%{
    /* opacity: 1; */
    -webkit-transform: rotate(10deg) ;
            transform: rotate(10deg) ;
  }
  
}
@-webkit-keyframes rotate {
  0%{
    opacity:0;
    /* transform: rotate(0); */
  }
  50%{
    opacity: 0.5;
    /* transform: rotate(180deg); */
  }
  100%{
    opacity:1;
    /* transform: rotate(360deg); */
  }
}
@keyframes rotate {
  0%{
    opacity:0;
    /* transform: rotate(0); */
  }
  50%{
    opacity: 0.5;
    /* transform: rotate(180deg); */
  }
  100%{
    opacity:1;
    /* transform: rotate(360deg); */
  }
}
@-webkit-keyframes scale{
  0%{
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes scale{
  0%{
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
#root{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width:100%;
  height:100%;
}
#beers{
  display: grid;
  grid-template-rows: auto auto auto;
  grid-template-columns: auto auto auto;
  background-color:white;
  max-width: 690px;
  max-height: 690px;
  place-content: center;
  margin-top:30px;
  border-radius: 30px;
  margin-bottom: 20px;
  border:1px solid palevioletred;
  box-shadow: 5px 5px 5px palevioletred; 
}
#beers>div{
  width:30vw;
  height:23vh;
  max-height: 230px;
  max-width: 230px;
  /* border:1px solid black; */
  padding:10px 10px;
  text-align: center;
}
#beers>div{
  color:white; 
}
#beers>div:hover{
  color:gray;
}
#beers>div>img{
  height:80%;
  max-height: 200px;
  cursor: pointer;
}
#beers>div>img:hover{
  -webkit-animation-name: shake;
          animation-name: shake;
  -webkit-animation-duration:0.2s;
          animation-duration:0.2s;
  -webkit-animation-iteration-count:infinite ;
          animation-iteration-count:infinite ;
}
#beers>div>p{
  font-size:15px;
  font-family: cursive;
}
header{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width:100%; 
  height:130px;
  overflow: visible;
  background-color: #4cc9f0;
  background-color: var(--blue);
  -webkit-align-items: center;
          align-items: center;
} 
header>#scoreboard{
  color:white;
  background-color: black;
  margin-right:20px;
  border-radius: 10px;
  width:250px;
  border:2px solid black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height:100px;
}
#scoreboard #names{
  display: -webkit-flex;
  display: flex;
}
#names>*{
  width:100%;
  text-align: center;
  border:3px solid white;
  padding:3px;
  font-weight: lighter;
  font-family: fantasy;
}
#score-name{
  border-top-left-radius: 10px;
}
#best-score-name{
  border-top-right-radius: 10px;
}
#scoreboard #scores{
  border: 3px solid white;
  height: 100%;
  border-bottom-right-radius:10px;
  border-bottom-left-radius:10px;
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  color:red;
  font-weight:bolder;
  font-size: 40px;
  font-family: 'Keania One';
}

header>p{
  font-size: 45px;
  line-height: 90px;
  margin-left:30px;
  color:white;
  font-weight: bold;
  font-family: monospace;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
header>p>b{
  font-weight: bold;
  color:#e9c46a;
  color:var(--yellow);
}
header>p>img{
  width:60px;
  margin-right:10px;
}

#game{
  width:100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  background-color: pink;
  height:100%;
}
.rotate{
  -webkit-animation-name: rotate;
          animation-name: rotate;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  /* animation-iteration-count: infinite; */
}
.rotate:hover{ 
  -webkit-animation-name: rotate !important; 
          animation-name: rotate !important;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  /* animation-iteration-count: infinite; */
}
#game-over-outer-div{
  position: fixed;
  width:100%;
  height:100%;
  background-color: transparent;
  display: grid;
  place-items: center;
  -webkit-transform: scale(0);
          transform: scale(0);
}
#game-over-div{
  margin-top:60px;
  border:10px solid black;
  -webkit-animation: scale;
          animation: scale;

  -webkit-animation-duration: 0.7s;

          animation-duration: 0.7s;
}
#game-over-div>p{
  position: absolute;
  /* width:100%; */
  text-align: center;
  z-index: 10;
  margin-left:60px;
  margin-top:10px;
  color:white;
  font-size: 60px;
  font-weight: bold;
  letter-spacing: 7px;
}
.game-over{
  -webkit-animation: scale;
          animation: scale;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-transform: scale(1) !important;
          transform: scale(1) !important;
  z-index: 20;
}
